import React from "react";
import { Grid, Row, Col } from "react-bootstrap";
import MenuChangers from "../Menu/Menu_changers";
import Team from "./Team";
import Logo from '../Partials/Logo';
import Title from "../Partials/Title";


const Info = () => {
    return (
        <Col lg={5} md={5}>
            <p className='pt-25 pl-25 justify' style={{ fontSize: '1.5rem', lineHeight: '1.6em' }} > 
                Las personas que forman parte de Change tienen una característica
                        en común: han visto la necesidad de hacer algo para cambiar la manera
                        que se enseña. En su mayoría, los integrantes de nuestro equipo tienen
                        una gran experiencia como profesores de inglés, pero también han realizado
                        capacitaciones y han formado parte de programa de diseño curricular.
                        Contamos con expertos en distintos campos y todos tienen experiencia
                        como profesionales en la enseñanza.
                </p>
        </Col>

    )
}

const ChangersView = () => {
    return (
        <Grid fluid id='changersView--content'>
            <Row>
                <MenuChangers />
                <Col md={11} lg={11} sm={10} xs={12} id='changers'>
                    <Logo />
                    <Title title={'Nuestro Equipo'} />
                    <Row>
                        <Info />
                        <Team />
                    </Row>
                </Col>
            </Row>
        </Grid>
    )
}

export default ChangersView


