import React from "react";
import MenuService from "../Menu/Menu_service";
import { Grid, Row, Col } from "react-bootstrap";
//import { FirstCard, SecondCard, ThirdCard } from "./Cards";
import { ServiceCard01, ServiceCard02, ServiceCard03 }  from "./NewCards";
//import { connect } from "redux-zero/react";
import Logo from "../Partials/Logo";
import Title from "../Partials/Title";
//import services01 from "../../img/icons/icons/services/icon services_01.png";
//import services02 from "../../img/icons/icons/services/icon services_02.png";
//import services03 from "../../img/icons/icons/services/icon services_03.png";

const Info = () => {
    return (
        <Row>
            <Col lg={5} md={5}>
                <p className='pt-25 pl-25' style={{ width: '80%' }} >
                    Todos nuestros servicios necesitan de una participación activa de nuestros clientes.
            </p>
            </Col>
        </Row>
    )
}

const ServiceContent = () => {
    return (
        <Col md={11} lg={11} sm={10} xs={12} id='service'>
            <Logo />
            <Title title={'Nuestros Servicios'} />
            <Info />

            <Row className='pt-25 pl-25'>
                <ServiceCard01 />
                <ServiceCard02 />
                <ServiceCard03 />
            </Row>
            {/*<div className="container--service">
                <h3> Todos nuestros servicios necesitan de una participación activa de nuestros clientes. </h3>
                <section className='pt-25' >
                    {/*<FirstCard showModal1={showModal1} />
                    <SecondCard showModal2={showModal2} />
    <ThirdCard showModal3={showModal3} />
                </section>
    </div>*/}
        </Col>
    )
}

const Service = () => {
    return (
        <Grid fluid id='serviceView--content'>
            <Row>
                <MenuService />
                <ServiceContent />
            </Row>
        </Grid>
    )
}

export default Service;
//const mapToProps = ({ showModal1, showModal2, showModal3 }) => ({ showModal1, showModal2, showModal3 });
//export default connect(mapToProps)(Service);


