import React from "react";
import { Col, Image } from "react-bootstrap";
import services01 from "../../img/icons/icons/services/icon services_01.png";
import services02 from "../../img/icons/icons/services/icon services_02.png";
import services03 from "../../img/icons/icons/services/icon services_03.png";

export const ServiceCard01 = () => {
    return(
        <Col lg={3} md={3} xs={3} sm={3} className='serviceCard' >
            <Image src={services01} style={{ width: '150px' }} />
            {/*<button> Ver más </button> */}
            <Modal01 />
            <p className='upperCase' ><strong> Capacitación </strong></p>
        </Col>
    )
}

export const ServiceCard02 = () => {
    return (
        <Col lg={3} md={3} xs={3} sm={3} className='serviceCard' >
            <Image src={services02} style={{ width: '150px' }} />
            {/*<button> Ver más </button> */}
            <Modal02  />
            <p className='upperCase' ><strong> Inglés para propósitos específicos </strong></p>
        </Col>
    )
}

export const ServiceCard03 = () => {
    return (
        <Col lg={3} md={3} xs={3} sm={3} className='serviceCard' >
            <Image src={services03} style={{ width: '150px' }} />
            {/*<button> Ver más </button> */}
            <Modal03  />
            <p className='upperCase' ><strong> English Club </strong></p>
        </Col>
    )
}

const Modal01 = () => {
    return(
        <div className="modalService">
            <input id="modal__trigger01" type="checkbox" />
            <label htmlFor="modal__trigger01" className='more' > Ver más </label>
            <div className="modal__overlay" role="dialog" aria-labelledby="modal__title" aria-describedby="modal_desc">
                <div className="modal__wrap">
                    <div className='modal__wrap--title' >
                        <Image src={services01} />
                        <h2 id="modal__title">Capacitación</h2>
                    </div>
                    <p id="modal__desc justify">
                        Nuestro programa de capacitación de docentes se basa en la técnica de Class Shadowing, en la cual el participante del entrenamiento toma parte activa del dictado de clases bajo la guía de un docente principal. Además, trabajamos con centros educativos para identificar sus objetivos estratégicos y sus necesidades y así diseñar programas de entrenamiento para sus docentes.
                    </p>
                    <label htmlFor="modal__trigger01"> Cerrar </label>
                </div>
            </div>
        </div>
    )
}

const Modal02 = () => {
    return (
        <div className="modalService">
            <input id="modal__trigger02" type="checkbox" />
            <label htmlFor="modal__trigger02" className='more' > Ver más </label>
            <div className="modal__overlay" role="dialog" aria-labelledby="modal__title" aria-describedby="modal_desc">
                <div className="modal__wrap">
                    <div className='modal__wrap--title' >
                        <Image src={services02} />
                        <h3 id="modal__title">Inglés para propósitos específicos</h3>
                    </div>
                    <p id="modal__desc justify">
                        Nuestro programa de capacitación de docentes se basa en la técnica de Class Shadowing, en la cual el participante del entrenamiento toma parte activa del dictado de clases bajo la guía de un docente principal. Además, trabajamos con centros educativos para identificar sus objetivos estratégicos y sus necesidades y así diseñar programas de entrenamiento para sus docentes.
                    </p>
                    <label htmlFor="modal__trigger02"> Cerrar </label>
                </div>
            </div>
        </div>
    )
}

const Modal03 = () => {
    return (
        <div className="modalService">
            <input id="modal__trigger03" type="checkbox" />
            <label htmlFor="modal__trigger03" className='more' > Ver más </label>
            <div className="modal__overlay" role="dialog" aria-labelledby="modal__title" aria-describedby="modal_desc">
                <div className="modal__wrap">
                    <div className='modal__wrap--title' >
                        <Image src={services03} />
                        <h2 id="modal__title">English Club</h2>
                    </div>
                    <p id="modal__desc justify">Ofrecemos programas de inglés en centros educativos luego del horario de clases. Trabajamos con jardines y colegios del estado en los llamados After School Programs. El concepto detrás de estos clubes de inmersión de inglés se basa en principios de aprendizaje ágiles y basados en proyectos.</p>
                    <label htmlFor="modal__trigger03"> Cerrar </label>
                </div>
            </div>
        </div>
    )
}




