import React from "react";
import { Row, Col } from "react-bootstrap";

const FormContact = () => {
    return (
        <Col lg={6} md={6} id='contactForm' className='pt-25' >
            <form className='pl-25'>
                <Row className='rowForm' >
                    <Col lg={2} md={2} ><span> Nombre </span></Col>
                    <Col lg={6} md={6} ><input type='text' required /></Col>
                </Row>
                <Row className='rowForm'>
                    <Col lg={2} md={2} ><span> Email </span></Col>
                    <Col lg={6} md={6} ><input type='email' required /></Col>
                </Row>
                <Row className='rowForm'>
                    <Col lg={2} md={2} ><span> Asunto </span></Col>
                    <Col lg={6} md={6} ><input type='text' required /></Col>
                </Row>
                <Row className='rowForm'>
                    <Col lg={2} md={2} ><span> Mensaje </span></Col>
                    <Col lg={6} md={6} ><textarea type='text' row='5' required /></Col>
                </Row>
                <Row>
                    <Col lg={4} md={4} xs={4} sm={4} lgOffset={4} mdOffset={4} smOffset={4} xsOffset={4} ><button type='submit'> Enviar </button></Col>
                </Row>
            </form>
        </Col>
    )
}

export default FormContact;
