import React from "react";
import { Grid, Row, Col, Image } from "react-bootstrap";
import FormContact from "./Form";
import MenuContact from "../Menu/Menu_contact";
import Logo from '../Partials/Logo';
import iconYoutube from "../../img/icons/icons/contact/icon_youtube.png";
import iconFacebook from "../../img/icons/icons/contact/icon_facebook.png";
import Title from "../Partials/Title";

const Social = () => {
    return (
        <Col lg={7} md={7}>
            {/*<div className="wrapper pt-25">
                <a href="https://www.facebook.com/changeperu/" target="_blank" rel='noopener noreferrer' className='fb'><i className="fa fa-3x fa-facebook-square"></i></a>
                <a href="https://www.youtube.com/channel/UC6dwKWSo_7D9KWUhF8kTSLg" target="_blank" rel='noopener noreferrer' className='ytb'><i className="fa fa-3x fa-youtube-square"></i></a>
                {/*<i className="fa fa-3x fa fa-youtube-square"></i>
            <i className="fa fa-3x fa-snapchat-square"></i>
    </div>*/}
            <Col lg={7} md={7} sm={6} xs={6} >
                <p className='pt-25'> No olvides visitarnos en nuestras redes </p>
            </Col>
            <Col lg={5} md={5} sm={6} xs={6} >
                <div className='pt-25' id='social' >
                    <a href="https://www.facebook.com/changeperu/" target="_blank" rel='noopener noreferrer' >
                        <Image src={iconFacebook} style={{ width: '30px', marginLeft: '10px'}} />
                    </a>
                    <a href="https://www.youtube.com/channel/UC6dwKWSo_7D9KWUhF8kTSLg" target="_blank" rel='noopener noreferrer'>
                        <Image src={iconYoutube} style={{ width: '40px', marginLeft: '10px' }} />
                    </a>
                </div>
            </Col>
        </Col>
    )
}

const Extra = () => {
    return (
        <Col lg={6} md={6}>          
            <p className='pt-25 pl-25' style={{width: '80%'}} >
                Cuéntanos tus proyectos y objetivos para poder brindarte la mejor opción para tu aprendizaje.
                <br/>
                Trabajando juntos podemos romper esquemas, podemos cambiar el sistema.
            </p>

            <Social />
        </Col>
    )
}


const Contact = () => {
    return (
        <Grid fluid id='contactView--content'>
            <Row>
                <MenuContact />
                <Col md={11} lg={11} sm={10} xs={12} id='contact'>
                    <Logo />
                    <Title title={ 'Contact' } />
                    <Row>
                        <Extra />
                        <FormContact />
                    </Row>
                </Col>
            </Row>
        </Grid>
    )
}

export default Contact;


