import React from "react";
import  logoChange  from "../../img/icons/icons/logo_resto_de_paginas.png";
import { Row, Col, Image } from "react-bootstrap";

const Logo = () => {
    return (
        <Row>
            <Col lg={6} md={6} xs={6} sm={6} >
                <section className='p-50'>
                    {/*<h1> Change </h1>
                    <p> cambiando la educación </p>*/}
                    <Image src={logoChange} style={{ width: '200px'}} />
                </section>
            </Col>
        </Row>
    )
}

export default Logo;


