import React from "react";
import { Row, Col } from "react-bootstrap";

const Title = ({ title }) => {
    return (
        <Row>
            <Col lg={6} md={6}>
                <h1 className='pl-25' > {title} </h1>
            </Col>
        </Row>
    )
}

export default Title;


